import { FontSize } from '../../../atoms'
import { Container } from '../../../layout'
import { PopUpDialog } from './PopUp'
import { ToggleButton } from './ToggleButton'

export interface PopUpButtonProps {
  buttonActiveColor?: string;
  buttonFirstLine: string;
  buttonFirstLineSize?: FontSize;
  buttonFirstLineWeight?: 'regular' | 'semibold' | 'medium' | 'bold';
  buttonShowArrow?: boolean;
  buttonSite?: 'unimarc' | 'alvi'
  buttonSecondLine?: string;
  buttonSecondLineSize?: FontSize;
  buttonSecondLineWeight?: 'regular' | 'semibold' | 'medium' | 'bold';
  buttonWidth?: string;
  children?: React.ReactNode;
  isActive: boolean;
  isLoggedIn?: boolean;
  popUpArrowPos?: 'left' | 'center' | 'right';
  popUpPos?: 'left' | 'middleLeft' | 'center' | 'middleRight' | 'right';
  onClickButton?: () => void;
}

export const PopUpButton = ({
  buttonActiveColor,
  buttonFirstLine,
  buttonFirstLineSize,
  buttonFirstLineWeight,
  buttonSecondLine,
  buttonSecondLineSize,
  buttonSecondLineWeight,
  children,
  isActive,
  isLoggedIn,
  popUpArrowPos,
  popUpPos,
  buttonShowArrow = false,
  buttonSite = 'unimarc',
  buttonWidth,
  onClickButton
}: PopUpButtonProps) => {

  return (
    <Container
      customWidth='max-content'
      justifyContent='center'
      position='relative'
    >
      <ToggleButton
        activeColor={buttonActiveColor}
        firstLine={buttonFirstLine}
        firstLineSize={buttonFirstLineSize}
        firstLineWeight={buttonFirstLineWeight}
        isActive={isActive}
        isLoggedIn={isLoggedIn}
        onClick={onClickButton}
        secondLine={buttonSecondLine}
        secondLineSize={buttonSecondLineSize}
        secondLineWeight={buttonSecondLineWeight}
        showArrow={buttonShowArrow}
        site={buttonSite}
        width={buttonWidth}
      />
      {isActive &&
        <PopUpDialog arrowPos={popUpArrowPos} position={popUpPos}>
          {children}
        </PopUpDialog>
      }
    </Container>
  )
}