import React from 'react'
import { Link, Text } from '../../../atoms'
import { v4 as uuid } from 'uuid'
import { Row } from '../../../layout'

interface PromoContentProps {
  repeat?: number
  isMobile?: boolean
  linkWrapper?: React.FunctionComponent;
  promoContent?: string
  promoLinkUrl?: string
  promoLinkText?: string
}

export const PromoComponent = ({
  repeat = 3,
  isMobile,
  linkWrapper,
  promoLinkUrl,
  promoContent,
  promoLinkText
}: PromoContentProps) => {
  const fontSize = isMobile ? 'sm' : 'md'

  return (
    <React.Fragment>
      {Array.from(Array(repeat).keys()).map(() => {
        return (
          <Row
            customWidth='max-content'
            key={uuid()}
            padding='0 10%'
          >
            <Text color='white' fontSize={fontSize}>
              {promoContent}
            </Text>
            <Link
              color='white'
              fontSize={fontSize}
              href={promoLinkUrl}
              label={promoLinkText || ''}
              wrapper={linkWrapper}
            />
          </Row>
        )
      })}
    </React.Fragment>
  )
}

export const PromoContent = React.memo(PromoComponent)