import React, { useCallback, useState } from 'react'
import { CategoriesData, CategoryData } from '..'
import {
  Icon,
  Link,
  Text
} from '../../../../atoms'
import { Row } from '../../../../layout'
import style from '../Categories.module.css'

interface CategoryLinkProps {
  categoriesData?: CategoriesData;
  category?: CategoryData;
  linkWrapper?: React.FunctionComponent;
  site?: string;
  subcategory?: CategoryData;
  handleClickCategory?: () => void;
  setIsSubMenuOpen?: (isOpen: boolean) => void;
  setSubcategory?: React.Dispatch<CategoryData>;
}

export const CategoryLink = ({
  categoriesData,
  category,
  linkWrapper,
  site = 'unimarc',
  subcategory,
  handleClickCategory,
  setIsSubMenuOpen,
  setSubcategory
}: CategoryLinkProps) => {
  const [isHover, setIsHover] = useState(false)

  const hoverCategory = useCallback(() => {
    setIsSubMenuOpen(true)
    if (!isHover) setIsHover(true)
    if (JSON?.stringify(category) !== JSON?.stringify(subcategory)) {
      setSubcategory(category)
    }
  },[isHover, subcategory])

  const siteHover = site === 'unimarc' ? 'unimarc' : 'alvi'

  return(
    <Link
      href={categoriesData.getCategoryUrl(category)}
      onClick={handleClickCategory}
      onMouseEnter={hoverCategory}
      underline='none'
      wrapper={linkWrapper}
    >
      <Row
        alignItems='center'
        clickable='pointer'
        customClassName={subcategory?.id === category.id ? style[`categories-hover-${siteHover}`] : ''}
        justifyContent='between'
        padding='7px 10px  7px 25px'
      >
        <Text clickable='pointer' fontSize='md'>
          {category.name}
        </Text>
        {subcategory?.id === category.id &&
          <Icon
            clickable='pointer'
            color='white'
            name='ArrowRight'
            sizes='xs'
          />
        }
      </Row>
    </Link>
  )
}